@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Vollkorn:400,600,700&display=swap);
.home-page-carousel-one .slide-content{position:relative;height:calc(100vh - 4.5rem - 10vh)}.home-page-carousel-one .slide-content img{display:block;width:100%;height:100%;object-fit:cover;opacity:.8;-webkit-transition:all .3s ease;transition:all .3s ease}.home-page-carousel-one .slide-content .text-block{position:absolute;bottom:15%;left:50%;-webkit-transform:translate(-50%, 0);transform:translate(-50%, 0);width:85%;color:#fff;font-size:1.5rem}.home-page-carousel-two .slick-slide{padding:0 .25rem}

.slick-arrow{z-index:10;width:3rem;height:3rem;line-height:1em;font-size:3rem;color:#f5f5f5;cursor:pointer}.slick-arrow:hover,.slick-arrow:focus{color:#fff;background-color:transparent}.slick-arrow::before{display:none}.slick-next{left:auto;right:1rem;-webkit-transform:translate(0, -50%) rotate(180deg);transform:translate(0, -50%) rotate(180deg)}.slick-prev{left:1rem;right:auto}

.card-item{margin:0 20px}

.section-title{padding:0 0 3rem;font-size:2.5rem;text-align:center}

.home-page .head-section-container{padding-left:0;padding-right:0}.home-page .section-with-cards{padding:6rem 0}.image-container{width:100%;overflow:hidden}img{width:100%}.section-intro{line-height:2;padding:2rem;text-align:center}.section-4{padding:2rem 0}

.scroll-to-top{position:fixed;bottom:3.75rem;right:1rem;z-index:1000;display:flex;justify-content:center;align-items:center;width:3rem;height:3rem;font-size:2rem;color:#ff8f00;cursor:pointer;border-radius:50%;background-color:#f5f5f5;box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12);-webkit-animation-name:showButton;animation-name:showButton;-webkit-animation-delay:0ms;animation-delay:0ms;-webkit-animation-duration:600ms;animation-duration:600ms;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-iteration-count:1;animation-iteration-count:1}@-webkit-keyframes showButton{0%{opacity:0}100%{opacity:1}}@keyframes showButton{0%{opacity:0}100%{opacity:1}}


.page-header{display:flex;justify-content:center;height:4.5rem}.brand-logo img{width:auto;height:calc(4.5rem - 1rem)}a{padding:1rem;font-size:1rem;color:#000;text-decoration:none}.options a{opacity:.8;-webkit-transition:all .3s ease;transition:all .3s ease}.options a:hover{padding:.5rem;-webkit-transform:scale(1.08);transform:scale(1.08);opacity:1;font-size:1.25rem}

.page-footer{z-index:0 !important;box-shadow:0px -2px 4px 1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12) !important}

html,body,h1,h2,h3,h4,h5,h6,p,ul,ol,li,input[type="submit"],button{margin:0;padding:0}main{display:block}ul,ol{list-style:none}input[type="submit"]{background-color:transparent;background-color:initial;border:none;cursor:pointer}input[type="search"]{-webkit-appearance:none;border:none}input[type="search"]::-webkit-search-decoration,input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-results-button,input[type="search"]::-webkit-search-results-decoration{display:none}*{-webkit-overflow-scrolling:touch}*,*::before,*::after{box-sizing:border-box;outline:none;background-repeat:no-repeat}body{line-height:normal;font-family:'Roboto', sans-serif;font-size:1rem;color:#000;text-align:left;-webkit-font-smoothing:antialiased}label{display:block}h1,h2,h3,h4,h5,h6{font-size:1rem}a{display:block;color:inherit;text-decoration:none}a:hover,a:active{color:inherit;text-decoration:none}img{display:block;max-width:100%}input,select{width:100%;border:none}textarea{display:block;width:100%;resize:none}button{display:block;background-color:transparent;border:none;cursor:pointer}button:focus{outline:none}button::-moz-focus-inner{padding:0;border:none}html,body{height:100%}body{display:flex;flex-direction:column}#root{display:flex;flex-direction:column;flex-grow:1}.App{display:flex;flex-direction:column;flex-grow:1}main{flex-grow:1;padding-top:4.5rem}

